section h2.tier-title {
  margin-top: 1rem;
  margin-bottom: 0.4em;
  font: 100 29px/1.3em "Roboto", sans-serif;
  text-align: center;
  margin-bottom: 0.4em;
  color: #000000;
  font-weight: 400;
  border-bottom: 1.3px solid #000000;
}

.animation-container.horizontal.sponsors {
  max-width: 70%;
}

@media only screen and (max-width: 640px) {
  .animation-container.horizontal.sponsors {
    display: block;
    left: -25px;
    width: calc(100% + 25px);
  }
}

@media only screen and (max-width: 640px) {
  .animation-container.horizontal.sponsors .animated.right {
    width: 100%;
  }
}
